import React from "react";
import {connect} from "react-redux";
import {getDeliveryAndPaymentHtml} from "eshop/modules/simfree/selectors";
import {setDeliveryAndPaymentComponentUid} from "eshop/modules/simfree/actions/app";
import {fetchDeliveryAndPaymentHtml} from "eshop/modules/simfree/actions/app";

class ProductCartPaymentAndDeliveryModeComponent extends React.Component {
    constructor(props) {
    console.log(props);
        super(props);
    }

    componentDidMount() {
        console.log("delivery and payment - mounting component");
        this.props.setDeliveryAndPaymentComponentUid(this.props.uid);
        this.props.fetchDeliveryAndPaymentHtml(this.props.uid);

    }

    toHTML(content) {
        return {__html: content};
    }

    render() {
        return (
            <div className="delivery-payment-method">
                {this.props.channels.sales !== 'POS' && this.props.deliveryAndPaymentHtml != null && this.props.deliveryAndPaymentHtml !== "" &&
                    <div dangerouslySetInnerHTML={this.toHTML(this.props.deliveryAndPaymentHtml)}/>
                    }
            </div>

        )
    }
}

const mapDispatchToProps = dispatch => ({
    setDeliveryAndPaymentComponentUid: (uid) => dispatch(setDeliveryAndPaymentComponentUid(uid)),
    fetchDeliveryAndPaymentHtml: (uid) => dispatch(fetchDeliveryAndPaymentHtml(uid))
});

const mapStateToProps = state => ({
    deliveryAndPaymentHtml : getDeliveryAndPaymentHtml(state)
});

export default  connect(mapStateToProps, mapDispatchToProps)(ProductCartPaymentAndDeliveryModeComponent);